/* src/App.css */
.app-container {
  align-items: center;
  max-width: 800px;
  margin: 10px auto;
  padding: 0;
  text-align: center;
  margin-left: 0;
}

.navbar {
  display: flex;
  flex-direction: row; /* Updated to arrange buttons in a row */
  align-items:center;
  margin-left: 50px;
  gap: 20px;
}

.logo {
  width: 100px;
  margin-bottom: -30px; /* Add margin between logo and buttons */
}

.mdh2 {
  margin-top: -25px;
}

button {
  padding: 10px 20px;
  background-color: rgb(79, 126, 110);
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}
