.onsite-container{
  display:flexbox;
  flex-direction: row;
margin-left: 15px;
}
.app {
  padding: 20px;
}

.match {
  margin-bottom: 20px;
}

.player {
  margin-bottom: 10px;
}

button {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: bold; /* Make the button text bold */
}

button:hover {
  background-color: #0056b3;
}

.para {
  color: blue;
  font-weight: bold;
  animation: blink 4s linear ; /* Blinking animation with a speed of 2s and infinite repetitions */
}

@keyframes blink {
  0%, 100% {
    opacity: 1; /* Fully visible at the start and end */
  }
  50% {
    opacity: 0; /* Completely transparent in the middle */
  }
}
