/* style/listPlayer.css */
.list-player-container {
    width: 100%;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 20px;
    overflow-x: auto; /* Add horizontal scroll if needed */
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  thead {
    background-color: #f2f2f2;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  button {
    background-color: #a89290;
    color: white;
    padding: 6px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 5px;
  }
  
  th, td, button {
    font-size: 14px;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 10px;
  }
  
  /* Adjust styles for smaller screens */
  @media (max-width: 600px) {
    th, td {
      padding: 6px;
    }
    button {
      padding: 6px 8px;
    }
  }
  