/* resultPage.css */

.container {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.heading {
  font-size: 24px;
  margin-bottom: 16px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
}

.row {
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  transition: background-color 0.3s;
}

.row:hover {
  background-color: #f5f5f5;
}

.cell {
  padding: 12px;
  text-align: center;
}

/* Adjusted style for Rank */
.rank {
  width: 40px; /* Adjust the width as needed */
}

.handicap {
  width: 60px;
}

.name {
  width: 200px;
  text-align: left; /* Align the text to the left */
  padding-left: 16px; /* Add left padding for better readability */
}

.win {
  width: 50px;
}

.total-wins {
  width: 60px;
}

/* Button styling */
button {
  padding: 10px 16px;
  margin-top: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

/* Message styling */
.success-message,
.error-message {
  margin-top: 16px;
  padding: 12px;
  border-radius: 4px;
  font-weight: bold;
}

.success-message {
  color: #28a745;
  background-color: #d4edda;
}

.error-message {
  color: #dc3545;
  background-color: #f8d7da;
}
