.available-container {
    margin: 20px;
  }
  
  h2 {
    font-size: 28px;
    margin-bottom: 15px;
    font-weight: bold; /* Make the heading text bold */
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  th, td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
    font-weight: bold; /* Make table header and cell text bold */
  }
  
  th {
    background-color: #f2f2f2;
  }
  
  tr:hover {
    background-color: #f5f5f5;
  }
  
  .loading-message {
    margin-top: 10px;
    font-style: italic;
    color: #666;
    font-weight: bold; /* Make the loading message text bold */
  }
  