/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #e6e6e6;
}

/* Tournament Bracket Styles */
.TournamentBracket {
  margin-left: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.TournamentButtons {
  display: flex;
  justify-content: space-around; /* Distribute buttons evenly */
  margin: 20px; /* Set equal margin on all sides */
}

/* Individual button styles (adjust as needed) */
.TournamentButtons button {
  padding: 10px 20px;
  background-color: rgb(79, 126, 110);
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.TournamentButtons button:hover {
  background-color: #45a049;
}


.round-1 .player {
  position: relative;
}

.player-number-label {
  position: absolute;
  left: -24px; /* Adjust the positioning as needed */
  top: 50%;
  transform: translateY(-50%);
  background-color: rgb(79, 126, 110); /* Adjust the background color */
  color: #fff; /* Adjust the text color */
  padding: 0; /* Remove padding */
  width: 20px; /* Set a fixed width */
  height: 20px; /* Set a fixed height */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%; /* Make it circular */
  font-weight: bold;
  font-size: 12px; /* Adjust font size */
}




.mainBox:not(:last-child) {
  margin-right: 20px;
}

/* Set background colors for main boxes based on odd and even */
mainBox:nth-child(even),
mainBox:nth-child(even) .subBox input {
    background-color: #e1f5fe; /* Light blue color for even main boxes and their input fields */
}

mainBox:nth-child(odd),
mainBox:nth-child(odd) .subBox input {
    background-color: #b2dfdb; /* Light green color for odd main boxes and their input fields */
}


/* Button and Input Field Styles */
input[type="number"],
button {
  margin-bottom: 0;
}

/* Sub Box Styles */
.subBox {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 0px;
}





/* App Styles */
.App {
  text-align: center;
  padding: 20px;
}

.SnookerTournament {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
}

/* Heading Styles */
h1 {
  color: #333;
}

h2 {
  color: #555;
}

label {
  display: block;
  margin-bottom: 10px;
}

/* Base Margin Values */
:root {
  --base-margin: 62px;
}

/* Calculate Margin for Even and Odd Players */
.round-2 .player.even {
  margin-top: calc(var(--base-margin) * 0.8);
}

.round-2 .player.odd {
  margin-bottom: calc(var(--base-margin) * 1.6);
}

.round-3 .player.even {
  margin-top: calc(var(--base-margin) * 2.2);
}

.round-3 .player.odd {
  margin-bottom: calc(var(--base-margin) * 4.3);
}

.round-4 .player.even {
  margin-top: calc(var(--base-margin) * 5);
}

.round-4 .player.odd {
  margin-bottom: calc(var(--base-margin) * 9.6);
}

.round-5 .player.even {
  margin-top: calc(var(--base-margin) * 10);
}

.round-5 .player.odd {
  margin-bottom: calc(var(--base-margin) * 19);
}

.round-6 .player.even {
  margin-top: calc(var(--base-margin) * 20);
}

.round-6 .player.odd {
  margin-bottom: calc(var(--base-margin) * 40);
}

/* Add more styles for other rounds as needed */

/* Input Styles */
input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/* Player Styles */
div.player.even {
  display: flex;
  flex-direction: row;
  align-items: center;
}

div.player.odd {
  display: flex;
  margin-bottom: 15px;
}

/* Name Input Styles */
input.name-input {
  width: 200px;
  margin-bottom: 0;
}

/* Handicap Input Styles */
input.handicap-input {
  width: 40px;
  margin-bottom: 0;
  margin-left: 5px;
  margin-right: 5px;
  font-weight: bold;
  color: rgb(0, 0, 0);
  font-size: 14px;
}

/* Calculated Handicap Input Styles */
input.calculated-handicap-input {
  flex-direction: row;
  font-weight: bold;
  color: rgb(0, 0, 0);
  font-size: 15px;
}

/* Checkbox Input Styles */
input.checkbox-input {
  width: 15px;
  margin-bottom: 0;
  margin-left: 2px;
  display: flex;
  justify-content: flex-end;
}

/* Button Styles */
button {
  background-color: rgb(79, 126, 110);
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

/* Occupied Name Input Styles */
.name-input.occupied {
  font-weight: bold;
  color: rgb(0, 0, 0);
  font-size: 15px; /* Adjust the font size as needed */
}

.NoOfPlayers{
display: flex;
margin-right: 100;
}